import React from "react";
import {
  Alert,
  Button,
  Col,
  Container,
  Row,
  Image,
  Tabs,
  Tab,
} from "react-bootstrap";

import Footer from "./Footer";
import NavBar from "./Nav";

import esquema5R1C from "./img/5R1C.svg";
import orientaciones from "./img/orientaciones.svg";

const HelpPage = (props) => (
  <Container fluid>
    <NavBar route={props.route} />
    <Row>
      <Col>
        <h1>EnvolventeCTE</h1>
        <p>
          <i>EnvolventeCTE</i> es una aplicacin web de ayuda al clculo de
          indicadores y parmetros de eficiencia energtica de la envolvente
          trmica, facilitando la aplicacin del CTE DB-HE (2019).
        </p>
        <p>Los indicadores y parmetros implementados son:</p>
        <ul>
          <li>
            <b>Transmitancia trmica global</b> (<b>K</b>)
          </li>
          <li>
            <b>Control solar</b> (
            <b>
              q<sub>sol;jul</sub>
            </b>
            )
          </li>
          <li>
            <b>
              Parmetros descriptivos de los elementos de la envolvente trmica
            </b>{" "}
            (<b>U</b>, <b>g</b>, <b>&psi;</b>...)
          </li>
        </ul>
        <Alert variant="warning">
          <b>NOTA:</b> Esta aplicacin y la informacin contenida en ella no
          tienen valor reglamentario.
        </Alert>
      </Col>
    </Row>
    <Row>
      <Col>
        <Tabs>
          <Tab eventKey={1} title="Envolvente trmica" className="pt-3">
            <Row>
              <Col>
                <h4>
                  Definicin e indicadores de calidad de la envolvente trmica
                </h4>
              </Col>
            </Row>
            <Row>
              <Col md={8}>
                <p>
                  El apartado de <i>Envolente</i> permite la definicin por
                  parte del usuario de la envolvente trmica y calcula los
                  indicadores de calidad (transmitancia trmica global y control
                  solar).
                </p>
                <p>
                  Los indicadores de calidad en la envolvente trmica se basan
                  en los descritos en la UNE EN ISO 13790:2008 (e ISO/FDIS
                  520161).
                </p>
                <ul>
                  <li>
                    <p>
                      El indicador de{" "}
                      <b>
                        transmitancia trmica global (<i>K</i>)
                      </b>{" "}
                      se basa en el coeficiente global de transmisin de calor (
                      <i>
                        H<sub>tr,adj</sub>
                      </i>
                      , apartado 8.3.1, ec. 17 de la{" "}
                      <i>UNE EN ISO 13790:2008</i> y apartado 6.6.5.2, ec. 108
                      de la <i>ISO/FDIS 52016-1</i>) repercutido por la
                      superficie de intercambio con el exterior.
                    </p>
                    <p>
                      <b>
                        Mide la capacidad global de evitar el intercambio de
                        calor por conduccin.
                      </b>
                    </p>
                  </li>
                  <li>
                    <p>
                      El indicador de{" "}
                      <b>
                        control solar (q<sub>sol;jul</sub>)
                      </b>{" "}
                      se basa en el{" "}
                      <i>
                        flujo de calor por ganancias solares, &Phi;
                        <sub>sol;k</sub>
                      </i>
                      , (apartado 11.3.2, ec. 43 de la{" "}
                      <i>UNE EN ISO 13790:2008</i> y apartado 6.5.13.2, ec. 69
                      de la <i>ISO/FDIS 52016-1</i>), despreciando la
                      reirradiacin al cielo, repercutido por la superficie til
                      considerada y considerando activadas las protecciones
                      solares mviles.
                    </p>
                    <p>
                      <b>
                        Mide la posibilidad de controlar las ganancias solares
                      </b>{" "}
                      (incluyendo el uso de dispositivos solares mviles y el
                      efecto de otros obstculos fijos o remotos).
                    </p>
                  </li>
                </ul>

                <p>
                  <small>
                    NOTA: En el indicador de ganancias solares no se han
                    considerado de forma separada las componentes difusa y
                    directa al tener en cuenta el efecto de las obstrucciones
                    solares, siguiendo el criterio de la{" "}
                    <i>UNE EN ISO 13790:2008</i> y no la formulacin de la{" "}
                    <i>ISO/FDIS 52016-1</i> (ver ec. 69, apartado 6.5.13.2).
                  </small>
                </p>
              </Col>
              <Col md={4}>
                <Image fluid alt="Esquema 5R1C EN 13790" src={esquema5R1C} />
              </Col>
            </Row>
          </Tab>
          <Tab eventKey={2} title="Clima" className="pt-3">
            <Row>
              <Col>
                <h4>Parmetros climticos</h4>
                <p>
                  En la seccin de <i>Parmetros</i> se recogen aquellos
                  parmetros de la envolvente trmica relacionados con el clima
                  exterior.
                </p>
                <p>
                  La aplicacin calcula, para superficies inclinadas y
                  orientadas, los valores de:
                </p>
                <ul>
                  <li>
                    <b>
                      Irradiacin acumulada mensual (
                      <i>
                        H<sub>sol;m</sub>
                      </i>
                      )
                    </b>
                  </li>
                  <li>
                    <b>
                      Irradiacin acumulada en el mes de julio (
                      <i>
                        H<sub>sol;jul</sub>
                      </i>
                      )
                    </b>
                  </li>
                  <li>
                    <b>
                      factor de reduccin para sombreamientos solares mviles (
                      <i>
                        f<sub>sh;with</sub>
                      </i>
                      )
                    </b>{" "}
                    de superficies inclinadas y orientadas.
                  </li>
                </ul>
                <p>
                  Los clculos para las distintas orientaciones y climas usan
                  valores obtenidos a partir de los archivos climticos de
                  referencia del <i>CTE DB-HE</i> y el procedimiento de la norma{" "}
                  <i>ISO/FDIS 520101:2016</i>.
                </p>
                <p>
                  <Button
                    variant="info"
                    size="xsmall"
                    href="http://www.codigotecnico.org/images/stories/pdf/ahorroEnergia/CTEdatosMET_20140418.zip"
                    target="_blank"
                  >
                    Descargar climas de referencia de codigotecnico.org{" "}
                  </Button>
                  .
                </p>
              </Col>
            </Row>
            <Row>
              <Col>
                <h4>Orientaciones de los elementos de la envolvente</h4>
                <Col md={{ span: 8, offset: 2 }}>
                  <Image
                    fluid
                    alt="Roseta de orientaciones de superficies"
                    src={orientaciones}
                  />
                </Col>
              </Col>
            </Row>
          </Tab>
          <Tab eventKey={3} title="Elementos" className="pt-3">
            <Row>
              <Col>
                <h4>Elementos de la envolvente trmica</h4>
                <p>
                  La aplicacin permite obtener algunos parmetros descriptivos
                  del comportamiento trmico de elementos de la envolvente
                  trmica a partir de sus caractersticas generales o parmetros
                  de diseo.
                </p>
                <p>
                  Por ejemplo, para los huecos se puede obtener: la
                  <i>transmitancia trmica</i> (U<sub>H</sub>), el{" "}
                  <i>factor solar del vidrio a incidencia normal</i> (g
                  <sub>gl;n</sub>), el <i>factor solar del hueco</i> (g
                  <sub>gl;wi</sub>), el{" "}
                  <i>
                    factor solar del hueco teniendo en cuenta los sombreamientos
                    solares mviles
                  </i>
                  (g<sub>gl;sh;wi</sub>).
                </p>
              </Col>
            </Row>
          </Tab>
        </Tabs>
      </Col>
    </Row>
    <Footer />
  </Container>
);

export default HelpPage;
